<template>
	<div id="single-blog">
		<h1>{{blog.title}}</h1>
		<article>{{blog.content}}</article>
		<p>作者：{{blog.author}}</p>
		<p>分类：</p>
		<ul>
			<li v-for="category in blog.categories">
				{{category}}
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		name:"single-blog",
		data(){
			return {
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			//./../static/posts.json
			//this.$http.get('http://jsonplaceholder.typicode.com/posts/'+this.id).then(function(data){
			this.$http.get('https://vueblog-10728.firebaseio.com/posts/'+this.id+".json")
			.then(function(data){
				return data.json();
				 //console.log(data);
				//this.blog = data.body;
			})
			.then(function(data){
				this.blog = data;
			})
		}
	}
</script>

<style>
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background-color: #eee;
		border: 1px dotted #aaa;
	}
</style>


